
.pokemon{
  width:100%;
  image-rendering: auto;
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -webkit-optimize-contrast;
      image-rendering: -moz-crisp-edges;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
  position: relative;
  bottom:0px;
  margin:5px;
}

.bulbasaur{
  --bulbasaur-width:120px;
  width:var(--bulbasaur-width);
  left:calc(-var(--bulbasaur-width)/2);
}

.charmander{
  --charmander-width:120px;
  width:var(--charmander-width);
  left:calc(-var(--charmander-width)/2);
}

.squirtle{
  --squirtle-width:120px;
  width:var(--squirtle-width);
  left:calc(-var(--squirtle-width)/2);
}

.chooser__bulbasaur{
  position:absolute;
  left:50%;
  -webkit-transform:translate(-50%,0%);
      -ms-transform:translate(-50%,0%);
          transform:translate(-50%,0%);
  top:10px;
}

.chooser__charmander{
  position:absolute;
  right:10%;
  top:130px;
}

.chooser__squirtle{
  position:absolute;
  left:10%;
  top:145px;
}

.Chooser{
  position:absolute;
  left:50%;

}

.headingtext{
  font-size:20px;
  text-align:center;
}

.controls{
  position:absolute;
  width:100%;
  height:10%;
  bottom:0px;
  text-align:center;
}


